* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.mainContainer {
  height: 80vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.main {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  background-color: #fdfdfd;

  display: grid;
  align-content: center;
  justify-content: center;
}

button {
  overflow: hidden;
  outline: none;
  color: white;
  font-size: 1.3em;
  border: 0;
  z-index: 1000;
  font-family: "Sanchez", serif;
}

section {
  display: flex;
  align-items: center;
  justify-content: center;
}

section div:first-child {
  position: absolute;
}

section div:nth-child(2) {
  position: absolute;
}

section div:last-child {
  position: absolute;
}

button,
.box {
  background-color: #45e8f9;
  height: 150px;
  width: 150px;
  border-radius: 50%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.external-box {
  height: 360px;
  width: 360px;
  border-color: #45e8f9;
  border-style: dashed;
  border-radius: 50%;
}

@keyframes blow {
  0% {
    height: 150px;
    width: 150px;
    transition-duration: 3s;
    background-color: #45e8f9;
  }
  50% {
    height: 360px;
    width: 360px;
    transition-duration: 2s;
    background-color: #45e8f9;
  }
  100% {
    height: 150px;
    width: 150px;
    transition-duration: 4s;
    background-color: #45e8f9;
  }
}

.start-animation {
  animation: blow 12s 20;
}
